import { omit } from 'lodash';
export const createChartStateRestorer = ({
  omitSignals = ['width', 'height', 'padding', 'autosize', 'background'],
  isActive = () => true
} = {}) => {
  let state;
  return {
    /**
     * Save Vega state
     * @public
     * @param newState - new state value
     */
    save: newState => {
      if (newState && isActive()) {
        state = {
          signals: omit(newState.signals, omitSignals || []),
          data: newState.data
        };
      }
    },

    /**
     * Restore Vega state
     * @public
     * @param restoreData - by default, we only recover signals,
     *        but if the data also needs to be recovered, this option should be set to true
     */
    restore: (restoreData = false) => isActive() && state ? omit(state, restoreData ? undefined : 'data') : null,

    /**
     *  Clear saved Vega state
     *
     *  @public
     */
    clear: () => {
      state = null;
    }
  };
};